@use "setting" as *;

.mainVisual {
    height: 100vh;
    position: relative;
    @media not all and (max-width: $pc){
        height: 491px;
    }
}

.mainVisual__txtWrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
    width: 100%;
    @media not all and (max-width: $pc){
        top: 70%;
    }
}
.slider-1 {
    position: relative;
    z-index: 1;
    width: 100%;
    // /*↑z-indexの値をh1のz-indexの値よりも小さくして背景に回す*/
    height: 100%; /*スライダー全体の縦幅を画面の高さいっぱい（100vh）にする*/
}

.slider-1__item01 {
    background: url(../../assets/images/TOP/mainvisual-sp.jpg);

    @media not all and (max-width: $pc){
        background: url(../../assets/images/TOP/mainvisual-pc.jpg);

    }
}

.slider-1__item02 {
    background: url(../../assets/images/TOP/golf-course-sp.jpg);
}

.slider-1__item03 {
    background: url(../../assets/images/TOP/golf-originalCourse.jpg);
}
.slider-1__item {
    width: 100%; /*各スライダー全体の横幅を画面の高さいっぱい（100%）にする*/
    height: 100vh; /*各スライダー全体の縦幅を画面の高さいっぱい（100vh）にする*/
    background-repeat: no-repeat; /*背景画像をリピートしない*/
    background-position: center; /*背景画像の位置を中央に*/
    background-size: cover; /*背景画像が.slider-item全体を覆い表示*/
    @media not all and (max-width: $pc){
        height: 491px;
        background-size: cover; /*背景画像が.slider-item全体を覆い表示*/
        background-repeat: no-repeat; /*背景画像をリピートしない*/
        background-position: center; /*背景画像の位置を中央に*/
    

    }
}

/*矢印の設定*/

/*戻る、次へ矢印の位置*/
.slick-prev,
.slick-next {
    position: absolute; /*絶対配置にする*/
    z-index: 3;
    top: 42%;
    cursor: pointer; /*マウスカーソルを指マークに*/
    outline: none; /*クリックをしたら出てくる枠線を消す*/
    border-top: 2px solid #ccc; /*矢印の色*/
    border-right: 2px solid #ccc; /*矢印の色*/
    height: 25px;
    width: 25px;
}

.slick-prev {
    /*戻る矢印の位置と形状*/
    left: 2.5%;
    transform: rotate(-135deg);
}

.slick-next {
    /*次へ矢印の位置と形状*/
    right: 2.5%;
    transform: rotate(45deg);
}

/*ドットナビゲーションの設定*/

.slick-dots {
    position: relative;
    z-index: 3;
    text-align: center;
    margin: -50px 0 0 0;
    top: 10%;
}

.slick-dots li {
    display: inline-block;
    margin: 0 5px;
}

.slick-dots button {
    color: transparent;
    outline: none;
    width: 8px; /*ドットボタンのサイズ*/
    height: 8px; /*ドットボタンのサイズ*/
    display: block;
    border-radius: 50%;
    background: #ccc; /*ドットボタンの色*/
}

.slick-dots .slick-active button {
    background: #333; /*ドットボタンの現在地表示の色*/
}

.siteTtl {
    margin-bottom: vw(115);
    font-family: ff(en);
    color: #fff;
    font-size: vw(26);
    @include ls(400);
    text-align: center;
    @media not all and (max-width: $pc){
        font-size: 26px;
        margin-bottom: 115px;
    }
}
.siteLogo {
    text-align: center;
}

.reservationBtn {
    position: fixed;
    z-index: z(999);
    width: vw(100);
    height: vw(207);
    background-color: c(main);
    bottom: vw(95);
    z-index: 999;
    box-shadow: 0 vw(3) vw(6) rgb(31, 30, 30);
    @media not all and (max-width: $pc){
        display: none;
    }
    a {
        writing-mode: vertical-rl;
        color: #fff;
        font-size: vw(22);
        @include ls(400);
        font-weight: bold;
        width: 100%;
        height: 100%;
        display: grid;
        place-items: center;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: vw(33);
            left: 51%;
            transform: translateX(-50%);
            @include san;
        }
    }
}
