@font-face {
    font-family: "icomoon";
    src:
    url('./../../assets/fonts/fontawesome/icomoon.ttf?vitycs') format('truetype'),
    url('./../../assets/fonts/fontawesome//icomoon.woff?vitycs') format('woff'),
    url('./../../assets/fonts/fontawesome/icomoon.svg?vitycs#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="fa-"],
[class*=" fa-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

